:root {
  --clr-black: #000;
  --clr-white: #fff;
  --clr-gray-500: #4d4f62;
  --clr-gray-400: #6a7178;
  --clr-gray-50: #fafafa;
  --clr-red: #ea5454;
  --clr-cyan: #44d3d2;
  --clr-blue: #549ef2;
  --clr-orange: #fcae4a;
  --ff-sans: "Poppins", Arial, Helvetica, sans-serif;
  --sp-8: 0.5rem;
  --sp-16: 1rem;
  --sp-24: 1.5rem;
  --sp-32: 2rem;
  --sp-40: 2.5rem;
  --fs-xlarge: 2.25rem;
  --fs-large: 1.5rem;
  --fs-med: 1.25rem;
  --fs-reg: 0.9375rem;
  --fs-small: 0.8125rem;
  --fs-xsmall: 0.6875rem;
  --fw-xlight: 200;
  --fw-reg: 400;
  --fw-semibold: 600;
  --letter-spacing: 0.015625rem;
  --text-preset-1: normal normal var(--fw-semibold) var(--fs-large) / 140%
    var(--ff-sans);
  --text-preset-2: normal normal var(--fw-xlight) var(--fs-large) / 140%
    var(--ff-sans);
  --text-preset-3: normal normal var(--fw-semibold) var(--fs-med) / 135%
    var(--ff-sans);
  --text-preset-4: normal normal var(--fw-reg) var(--fs-reg) / 140%
    var(--ff-sans);
  --text-preset-5: normal normal var(--fw-reg) var(--fs-small) / 160%
    var(--ff-sans);
}

@media (min-width: 48em) {
  :root {
    --text-preset-1: normal normal var(--fw-semibold) var(--fs-xlarge) / 140%
      var(--ff-sans);
    --text-preset-2: normal normal var(--fw-xlight) var(--fs-xlarge) / 140%
      var(--ff-sans);
  }
}
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
}

body {
  position: relative;
  font-family: var(--ff-sans);
  padding: 3.5rem 1.875rem;
}
@media (min-width: 48em) {
  body {
    padding: 5rem 3.375rem;
  }
}
@media (min-width: 64em) {
  body {
    padding-top: 6.375rem;
  }
}

html,
body {
  margin: 0px auto;
  width: 100%;
  box-sizing: border-box;
  background: var(--clr-gray-50);
  color: var(--clr-gray-500);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

