.card {
  display: flex;
  flex-direction: column;
  background-color: var(--clr-white);
  border-radius: var(--sp-8);
  box-shadow: 0 15px 30px -11px rgba(131, 166, 210, 0.5);
  overflow: hidden;
  height: 15.625rem;
  width: 19.625rem;
  margin: auto;
}
.card__container {
  display: grid;
  place-content: center;
  gap: var(--sp-32);
  max-width: 69.625rem;
  margin: auto;
}
@media (min-width: 48em) {
  .card__container {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}
@media (min-width: 64em) {
  .card__container {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
.card.cyan .card__inner {
  border-color: var(--clr-cyan);
}
.card.red .card__inner {
  border-color: var(--clr-red);
}
.card.orange .card__inner {
  border-color: var(--clr-orange);
}
.card.blue .card__inner {
  border-color: var(--clr-blue);
}
.card__inner {
  padding: var(--sp-32);
  border-top: 5px solid transparent;
  display: flex;
  flex-direction: column;
  gap: var(--sp-32);
  height: 100%;
}
.card__header h2 {
  font: var(--text-preset-3);
  margin-bottom: var(--sp-8);
}
.card__header p {
  font: var(--text-preset-5);
  color: var(--clr-gray-400);
}
.card__img img {
  display: block;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  float: right;
}
@media (min-width: 48em) {
  .card.cyan {
    grid-area: 2/1/3/2;
  }
  .card.red {
    grid-area: 1/1/2/3;
  }
  .card.orange {
    grid-area: 2/2/3/3;
  }
  .card.blue {
    grid-area: 3/1/4/3;
  }
}
@media (min-width: 64em) {
  .card {
    width: 21.875rem;
  }
  .card.cyan {
    grid-area: 1/1/3/2;
  }
  .card.red {
    grid-area: 1/2/2/3;
  }
  .card.orange {
    grid-area: 2/2/3/3;
  }
  .card.blue {
    grid-area: 1/3/3/4;
  }
}

.attribution {
  margin-top: 2.875rem;
  font-size: var(--fs-xsmall);
  text-align: center;
  color: var(--clr-gray-500);
}

.attribution a {
  color: blue;
}

.hero {
  color: var(--clr-gray-500);
  text-align: center;
  margin-inline: auto;
  margin-bottom: 3.75rem;
  max-width: 33.75rem;
}
.hero h1 {
  font: var(--text-preset-2);
  letter-spacing: var(--letter-spacing);
  margin: 0 0 var(--sp-16);
}
.hero h1 span {
  font: var(--text-preset-1);
}
.hero p {
  font: var(--text-preset-4);
}
@media (min-width: 48em) {
  .hero {
    margin-bottom: 4.625rem;
  }
}